                            <el-col :span="24">
                                <div class="title grid-content bg-purple-dark">. PERSONAL STATEMENT </div>
                            </el-col>
                            NOTES: Write briefly your reasons for applying to study at Nanjing University, outlining the
                            curricula which
                            you are interested in, your career aspirations and any other information you consider
                            relevant, within 500
                            Chinese characters.
                            500
                        </el-row>
                        <el-form-item prop="personalStatement">
                            <el-col :span="23">
                                <el-input type="textarea" placeholder="" v-model="form.personalStatement"
                                          class="personalStatement" style="width: 1050px"
                                          :clearable="true">
                                </el-input>
                            </el-col>
                        </el-form-item>
                        <el-row>

                        </el-row>
                    </div>
                </el-collapse-item>
            </el-collapse>
            <!---->
            <el-col :span="24">
                <div class="prompt">
                    <i class="el-icon-info"></i>
                    <span></span>
                </div>
                <el-button type="primary" icon="el-icon-upload2" class="confirmUpload" @click="submitApplication">
                </el-button>
                <el-button type="primary" icon="el-icon-document" class="saveEmail" @click="tempSaveApplication">
                </el-button>
            </el-col>
        </el-form>
    </el-card>
</template>

<script>
    import {getBasicInfo, sendApplication, getMajors} from "utils/api";
    import highSchools from 'utils/highSchools.ts';

    export default {
        data() {
            return {
                options: highSchools,
                form: {
                    firstName: '',
                    lastName: '',
                    needSimplification: false,
                    sex: 1,
                    birthDate: '',
                    email: '',
                    mtpNumber: '',
                    idCardNumber: '',
                    highSchool: [],
                    graduationYear: '',
                    address: '',
                    postalCode: '',
                    phoneNumber: {
                        home: '',
                        mobile: '',
                        fax: ''
                    },
                    curriculumChoices: {
                        firstChoice: '',
                        secondChoice: '',
                        thirdChoice: '',
                        fourthChoice: '',
                    },
                    artOrSci: 0,
                    acceptAssignment: false,
                    primarySchool: {
                        name: '',
                        region: '',
                        startDate: '',
                        endDate: ''
                    },
                    juniorMiddleSchool: {
                        name: '',
                        region: '',
                        startDate: '',
                        endDate: ''
                    },
                    seniorMiddleSchool: {
                        name: '',
                        region: '',
                        startDate: '',
                        endDate: ''
                    },
                    results: {
                        chinese: 0,
                        math: 0,
                        english: 0,
                        socials: 0,
                        sciences: 0
                    },
                    actualLevelPoints: {
                        chinese: 0,
                        math: 0,
                        english: 0,
                        socials: 0,
                        sciences: 0
                    },
                    levelRange: {
                        chinese: 0,
                        math: 0,
                        english: 0,
                        socials: 0,
                        sciences: 0
                    },
                    singleSubjectCriteria: {
                        chinese: '',
                        math: '',
                        english: '',
                        socials: '',
                        sciences: ''
                    },
                    totalLevelPoints: 0,
                    criteriaLevel: '',
                    personalStatement: ''
                },
                familyParticulars: {
                    members: [
                        {
                            relationship: '',
                            name: '',
                            companyName: '',
                            occupation: '',
                            phoneNumber: ''
                        }
                    ]
                },
                activities: {
                    activity: [
                        {
                            organization: '',
                            award: '',
                            attendingDate: ''
                        }
                    ]
                },
                rules: {
                    needSimplification: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    sex: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    birthDate: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    highSchool: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    graduationYear: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    address: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    postalCode: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    mobile: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    firstChoice: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    secondChoice: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    artOrSci: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    acceptAssignment: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    region: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    startDate: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    endDate: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    chinese: [
                        {required: true, message: "", trigger: 'change'},
                    ],
                    math: [
                        {required: true, message: "", trigger: 'change'},
                    ],
                    english: [
                        {required: true, message: "", trigger: 'change'},
                    ],
                    personalStatement: [
                        {required: true, message: "", trigger: 'blur'},
                        {max: 500, message: "500", trigger: 'blur'}
                    ],
                },
                majors: []
            };
        },
        mounted() {
            this.$nextTick(() => {
                this.getApplicationInfo();
                this.getMajors();
            })
        },
        methods: {
            removeMember(item) {
                let index = this.familyParticulars.members.indexOf(item);
                if (index !== -1) {
                    this.familyParticulars.members.splice(index, 1)
                }
            },
            addMember() {
                this.familyParticulars.members.push({
                    relationship: '',
                    name: '',
                    companyName: '',
                    occupation: '',
                    mobilePhoneNumber: '',
                    // key: Date.now()
                });
            },
            removeActivity(item) {
                let index = this.activities.activity.indexOf(item);
                if (index !== -1) {
                    this.activities.activity.splice(index, 1)
                }
            },
            addActivity() {
                this.activities.activity.push({
                    organization: '',
                    award: '',
                    attendingDate: '',
                    // key: Date.now()
                });
            },
            submitApplication() {
                let information = Object.assign({}, this.form);
                information.highSchool = this.form.highSchool.toString();
                information.familyParticulars = this.familyParticulars.members;
                information.activities = this.activities.activity;
                if (information.birthDate.indexOf('T') >= 0) {
                    information.birthDate = information.birthDate.substring(0, information.birthDate.indexOf('T'))
                }
                console.log(information);
                sendApplication(information)
                    .then((res) => {
                        if (res.data.succeed) {
                            this.$message({
                                message: '',
                                type: 'success'
                            })
                        } else {
                            if (res.data.msg === '') {
                                this.$message({
                                    message: '',
                                    type: 'error'
                                })
                            } else {
                                this.$message({
                                    message: res.data.msg,
                                    type: 'error'
                                })
                            }
                        }
                    })
                    .catch((err) => {
                        this.$message({
                            message: '',
                            type: 'error'
                        })
                    })
            },
            getApplicationInfo() {
                let _this = this;
                getBasicInfo()
                    .then((res) => {
                        let info = res.data;
                        this.form.firstName = info.firstName;
                        this.form.lastName = info.lastName;
                        this.form.idCardNumber = info.idCardNumber;
                        this.form.email = info.email;
                        if (info.highSchool !== '') {
                            this.form.highSchool = info.highSchool.split(',');
                        }
                        if (info.birthDate !== null) {
                            this.form.birthDate = info.birthDate;
                        }
                        this.form.address = info.address;
                        // return _this;
                    })
                    // .then(() => {
                    //     _this.getTempSavedApplication();
                    // })
                    .catch((err) => {
                        this.$message({
                            message: err,
                            type: 'error'
                        })
                    })
            },
            tempSaveApplication() {
                // 
                let storage = window.localStorage;
                let information = Object.assign({}, this.form);
                information.highSchool = this.form.highSchool.toString();
                information.familyParticulars = this.familyParticulars.members.toString();
                information.activities = this.activities.activity.toString();
                for (let key in information) {
                    storage.setItem(key, information[key]);
                }
            },
            getTempSavedApplication() {
                // 
                let storage = window.localStorage;
                for (let key in this.form) {
                    if (key === 'highSchool') {
                        this.form.highSchool = storage.getItem(key).split(',');
                    } else if (key === 'familyParticulars') {
                        this.familyParticulars = {
                            members: storage.getItem(key).split(',')
                        }
                    } else if (key === 'activities') {
                        this.activities = {
                            activity: storage.getItem(key).split(',')
                        }
                    } else {
                        this.form[key] = storage.getItem(key);
                    }
                }
            },
            getSubjectCriteria() {
                return [{
                    value: 'TOP_CRITERIA',
                    label: ''
                }, {
                    value: 'HEAD_CRITERIA',
                    label: ''
                }, {
                    value: 'AVERAGE_CRITERIA',
                    label: ''
                }, {
                    value: 'BACK_CRITERIA',
                    label: ''
                }, {
                    value: 'BOTTOM_CRITERIA',
                    label: ''
                }];
            },
            getMajors() {
                let majors = [];
                getMajors()
                    .then((res) => {
                        res.data.forEach((major) => {
                            majors.push({
                                label: major.name,
                                value: major.name
                            });
                        })
                    })
                    .catch((err) => {
                        this.$message({
                            message: err,
                            type: 'error'
                        })
                    });
                this.majors = majors;
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .wrapper {
        margin: 25px 20px;

        .el-row {
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .el-col {
            border-radius: 4px;
        }

        .bg-purple-dark {
            background: #BEADC6;
        }

        .bg-purple {
            background: #d3dce6;
        }

        .bg-purple-light {
            background: #e5e9f2;
        }

        .grid-content {
            border-radius: 4px;
            min-height: 36px;
        }

        .row-bg {
            padding: 10px 0;
            background-color: #f9fafc;
        }

        .title {
            padding-top: 10px;
            font-weight: bold;
        }

        .title1 {
            margin: 20px;
        }

        .title2 {
            margin: 20px;
        }

        .toolbar {
            margin: 0;
            padding: 8px 5px;
            border-radius: 4px;
            background: white;

            .el-upload-tip {
                margin-left: 10px;
                color: #a6a9ad;
            }
        }

        .saveEmail {
            float: right;
            margin-top: -25px;
            margin-bottom: 10px;
        }

        .confirmUpload {
            float: right;
            margin-top: -25px;
            margin-left: 10px;
            margin-bottom: 10px;
        }

        .prompt {
            margin-top: 8px;
            margin-left: 20px;
            color: #a6a9ad;
        }
    }
</style>

<style>
    .el-textarea__inner {
        resize: none !important;
        width: 100%;
        height: 390px;
    }
</style>



